/* DigitalClock.css */

.clock-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: #333;
  color: #fff;
  padding: 20px;
  border-radius: 10px;
  width: 250px;
  margin: 20px auto;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
}

.clock-title {
  font-size: 1.2em;
  margin-bottom: 10px;
}

.clock-time {
  font-size: 2.5em;
  font-weight: bold;
  letter-spacing: 0.05em;
  font-family: 'Courier New', Courier, monospace;
}
